.message {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-right: 10px;
    margin-bottom: 40px;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    margin-bottom: 5px;
}

.imageAndText img {
    height: 70px;
    align-self: flex-end;
}

.text {
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    display: flex;
    letter-spacing: 0;
    flex-direction: column;
    margin-right: 10px;
    color: rgb(255, 255, 255);
    border-radius: 15px;
    padding: 5px 20px;
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1),0 5px 20px 0 rgba(29, 33, 38, 0.03);
    background: rgb(0, 102, 204);
}

/*.text:after {*/ /*}*/

.name {
    color: rgb(255, 255, 255);
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    font-weight: 600;
    line-height: 22px;
    letter-spacing: 0;
    text-align: right;
    margin-bottom: 10px;
}

